	module.exports = {"data":{"markdownRemark":{"html":"<p>Listen in on my conversation with Ido Abramovich, a technical trainer turned training business owner in Israel. Ido gives us insight into his growing training business, where he’s made the move from full time employee to entrepreneur with a business partner and employees. Ido shares insight into the business model he’s adopted, this idea of Training as a Service that he uses to help lessen the risk for clients, his development stack, and his general thoughts on what it takes to run and grow a business in a niche market.</p>\n<iframe src=\"https://simplecast.com/e/42494?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>Remember to show Ido some love for sharing his journey with us. He’s on Twitter: <a href=\"https://twitter.com/idoabra\">@idoabra</a> Shownotes available on <a href=\"https://dearinstructionaldesigner.simplecast.fm/\">https://dearinstructionaldesigner.simplecast.fm/</a></p>","frontmatter":{"date":"August 04, 2016","path":"/blog/the-dearid-show-interview-with-ido-abramovich","title":"The DearID Show: Interview with Ido Abramovich","tags":["business","dear id","entrepreneurship","TAAS","technical training","training as a service"],"categories":["Dear ID Show"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3,"src":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg","srcSet":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c7d05.jpg 203w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-784f5.jpg 405w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg 810w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-adada.jpg 1215w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-1c372.jpg 1500w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

